

* {
	box-sizing: border-box;
}

body {
	background: #000;
}

h1{
	color: #fff;
	font-size: 3.5em;
	margin: 30px;
	text-align: center;
	font-family: 'Permanent Marker', cursive;
}

.gallery {
	display: flex;
	flex-wrap: wrap;
	padding: 20px;
	font-family: sans-serif;
	margin: 5px;

	img{
		margin: 8px;
		flex-grow: 1;
		width: 300px;
		height: 80%;
		border: 4px solid #fff;
	}
}

